@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
.login{margin-left:15px;display:flex;align-items:center;position:relative}.login-blur{position:fixed;-webkit-backdrop-filter:blur(5px);backdrop-filter:blur(5px);width:100vw;height:100vh;z-index:90;top:0;left:0}.login-wrapper{position:absolute;top:50px;max-width:450px;min-width:300px;padding:25px;background-color:#000;border-radius:8px;z-index:100;text-align:center}.login-wrapper:before{border:solid;border-color:#000 transparent;border-width:0px 6px 6px 6px;top:-6px;content:"";left:30px;position:absolute;z-index:99}.login__title{font-weight:800;text-transform:uppercase;font-size:18px;cursor:pointer}.login__title:hover{color:#ff8f0f}.login__inputs{display:flex;flex-direction:column;grid-gap:10px;gap:10px}.login__input{width:100%;background-color:#272727;color:#fff;border:rgba(39,39,39,0);font-size:16px;outline:none;border-radius:4px;padding:10px 15px;cursor:pointer}.login__btn{display:inline-block;width:100%;text-align:center;font-size:16px;text-transform:uppercase;padding:9px 14px;background-color:transparent;border:2px solid #fff;border-radius:5px;cursor:pointer;transition:.2s ease-out;font-weight:600}.login__btn:hover{background-color:#fff;color:#000}.login__text-btn{margin-top:15px;cursor:pointer}.login__text-btn:hover{color:#ff8f0f}
.header{display:flex;justify-content:space-between;margin-top:30px;align-items:center}.header__logo-login{display:flex;justify-content:space-between}.header__logo{font-size:40px;text-transform:uppercase;color:#ff8f0f;text-decoration:none;font-weight:900}.header__cart{color:#fff;text-decoration:none;display:flex;align-items:center}.header__cart-image{width:40px;height:40px}.header__cart-numbers{color:#ff8f0f;font-weight:600;padding:0 5px;font-size:18px}
.product-list__item{position:relative;max-width:320px;min-width:320px;min-height:250px}.product-list__image{position:absolute;max-width:220px;max-height:220px;right:0;bottom:0;z-index:-1}.product-list__image .image{width:100%}.product-list__name{font-size:36px;text-transform:uppercase;line-height:33px;font-weight:900;height:66px}.product-list__price{font-size:36px;line-height:36px;margin:10px 0;font-weight:300}.product-list__ruble{vertical-align:super;font-size:15px;line-height:15px}.product-list__description{font-size:14px;line-height:14px;font-weight:500;width:50%}.product-list__btn{position:absolute;bottom:0px;font-size:20px;background:rgba(34,34,34,0);border:2px solid #fff;border-radius:5px;padding:8px 14px;color:#fff;text-transform:uppercase;font-weight:600;cursor:pointer}.product-list__btn:hover{color:#222;background-color:#fff}


.product-list{display:flex;flex-wrap:wrap;grid-gap:30px 105px;gap:30px 105px}.product-list__title{font-size:128px;text-transform:uppercase;font-weight:900;margin:50px 0 20px}
.filter-buttons{display:flex;justify-content:space-between;margin-top:50px}.filter-buttons__item{padding:10px 20px;font-size:30px;font-weight:900;color:#fff;text-transform:uppercase;cursor:pointer}.filter-buttons__item:hover{color:#ff8f0f}.filter-btn-active{color:#ff8f0f}
.cart__title{font-size:128px;text-transform:uppercase;font-weight:900;margin:50px 0 20px}.cart__subtitle{font-size:30px;text-transform:uppercase;font-weight:900;margin:10px 0 20px}.cart-products{display:flex;flex-wrap:wrap;grid-gap:70px 48.5px;gap:70px 48.5px;margin-top:70px}.cart-products__item{width:195px;position:relative;padding:0 20px 15px 20px;background-color:rgba(0,0,0,.25);text-align:center;border-radius:10px}.cart-products__image{min-height:150px;max-width:150px;margin-top:-35px}.cart-products__remove{color:#ff8f0f;font-size:30px;position:absolute;width:30px;right:0;top:0;cursor:pointer}.cart-products__name{text-transform:uppercase;font-weight:900;font-size:18px;height:36px}.cart-products__counter{display:flex;justify-content:space-around;padding:20px 20px;font-size:22px}.cart-products__counter-less,.cart-products__counter-more{width:30px;cursor:pointer}.cart-products__price{font-size:18px}.cart-products__ruble{vertical-align:super;font-size:10px}.client-data{display:flex;flex-direction:column;width:300px;grid-gap:15px;gap:15px;font-family:"Montserrat",sans-serif}.client-data__item{background:transparent;color:#fff;border:0;border-bottom:1px solid #fff;height:33px;outline:0;padding:0 5px}.client-data__btn-sumbit{outline:0;bottom:0px;font-size:20px;background:rgba(34,34,34,0);border:2px solid #fff;border-radius:5px;padding:8px 14px;color:#fff;text-transform:uppercase;font-weight:600;cursor:pointer;width:228px}.client-data__btn-sumbit:hover{color:#000;background:#fff}
*,*:before,*:after{box-sizing:border-box}html,body,div,span,object,iframe,figure,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,code,em,img,small,strike,strong,sub,sup,tt,b,u,i,ol,ul,li,fieldset,form,label,table,caption,tbody,tfoot,thead,tr,th,td,main,canvas,embed,footer,header,nav,section,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline;text-rendering:optimizeLegibility;-webkit-font-smoothing:antialiased;-webkit-text-size-adjust:none;-moz-text-size-adjust:none;text-size-adjust:none}footer,header,nav,section,main{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:"";content:none}table{border-collapse:collapse;border-spacing:0}input{-webkit-appearance:none;border-radius:0}.body{background:#222 url("https://firebasestorage.googleapis.com/v0/b/store-project-de72e.appspot.com/o/store-project%2Fimages%2Fbg.jpg?alt=media&token=8523fd90-867c-42de-bd7e-c7125d1de8ee") repeat;color:#fff;overflow-x:hidden;font-family:"Montserrat",sans-serif}.container{max-width:1170px;height:100%;margin:auto}.image{width:100%}.mt50px{margin-top:50px}.fw300{font-weight:300}.fw600{font-weight:600}.text_orange{color:#ff8f0f}.d-none{display:none}.d-block{display:block}
